.center {
    padding-left: calc(50% - 250px);
    padding-right: calc(50% - 250px);
    display: flex;
    justify-content: center;
    gap: 15px;
}

.pageNumber {
    cursor: pointer;
    margin-bottom: 15px;
    padding: 5px;
}
.pageNumber:hover {
    border-bottom: 1px solid black;
    padding-bottom: 4px;
}
